:host{
    --default-background-color: #005ea2;
    --default-hover-background: #1a4480;
    --default-active-background: #162e51;
    --default-focus-outline-color: #2491ff;
    --default-font-color: #fff;
    
    --secondary-background-color: #d83933;
    --secondary-hover-background: #b50909;
    --secondary-active-background: #8b0a03;
    --secondary-focus-outline-color: #2491ff;
    --secondary-font-color: #fff;

    --unstyled-font-color: #005ea2;
    --unstyled-background-color: transparent;

    --disabled-color: #c9c9c9;
    --font-family: Source Sans Pro Web,Helvetica Neue,Helvetica,Roboto,Arial,sans-serif;
    --font-size: 1.06rem;
    --font-weight: 700;
    --button-padding: 0.75rem 1.25rem;

    --primary-color: #0071BC;
    --secondary-color: #02BFE7;
    --danger-color: #E51C3E;
    --success-color: #2E8540;
    --warning-color: #FDB81E;
    --base-color: rgb(50, 50, 51);
    --inverse-color: lightgrey;
    --default-color: #E3E3E3;

    --primary-color-hover: #205493;
    --secondary-color-hover: #00A6D2;
    --success-color-hover: #266E35;
    --warning-color-hover: #C05600;
    --danger-color-hover: #B31E22;

    --primary-color-active: #112E51;
    --secondary-color-active: #046B99;
    --success-color-active: #174320;
    --warning-color-active: #775540;
    --danger-color-active: #981B1F;

    --box-shadow-color: #3D94D0;

    display: block;
}

.svg-right {
    margin-left: 10px;
}
.svg-left {
    margin-right: 10px;
}

button {
    margin-top: 10%;
}

.storybook-button {
    font-family: 'Nunito Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-weight: 700;
    border: 0;
    border-radius: 5px;
    cursor: pointer;
    display: inline-block;
    line-height: 1;
}

.storybook-button--small {
    font-size: 12px;
    padding: 10px 16px;
}
.storybook-button--medium {
    font-size: 14px;
    padding: 11px 20px;
}
.storybook-button--large {
    font-size: 16px;
    padding: 12px 24px;
}
.storybook-button--extralarge {
    font-size: 16px;
    padding: 20px 32px;
}

/* Button primary design */
.storybook-button--primary-filled {
    color: white;
    background-color: var(--primary-color);
}
.storybook-button--primary-hover {
    color: white;
    background-color: var(--primary-color-hover);
}
.storybook-button--primary-active {
    color: white;
    background-color: var(--primary-color-active);
}
.storybook-button--primary-focus {
    color: white;
    outline: 2px solid var(--box-shadow-color);
    outline-offset: .25em;
    background-color: var(--primary-color);
}
.storybook-button--primary-default {
    color: #4F4F4F;
    background-color: var(--default-color);
}
.storybook-button--primary-filled-outlined {
    color: var(--primary-color);
    background: transparent;
    border: 2px solid var(--primary-color);
}
.storybook-button--primary-hover-outlined {
    color: var(--primary-color-hover);
    background: transparent;
    border: 2px solid var(--primary-color-hover);
}
.storybook-button--primary-active-outlined {
    color: var(--primary-color-active);
    background: transparent;
    border: 2px solid var(--primary-color-active);
}
.storybook-button--primary-focus-outlined {
    color: var(--primary-color);
    background: transparent;
    outline: 2px solid var(--box-shadow-color);
    outline-offset: .25em;
    border: 2px solid var(--success-color);
}
.storybook-button--primary-default-outlined {
    color: #4F4F4F;
    background: transparent;
    border: 2px solid var(--default-color);
}
.storybook-button--primary-filled-link {
    color: var(--primary-color);
    background: transparent;
}
.storybook-button--primary-hover-link {
    color: var(--primary-color-hover);
    background: transparent;
}
.storybook-button--primary-active-link {
    color: var(--primary-color-active);
    background: transparent;
}
.storybook-button--primary-focus-link {
    color: var(--primary-color);
    background: transparent;
    border: 2px solid #3D94D0;
}
.storybook-button--primary-default-link {
    color: #4F4F4F;
    background: transparent;
}
/* End of Button primary design */

